.button {
  display: inline-flex;
  padding: 8px 15px;

  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $color-default-white;

  background-color: #A5C7F9;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  transform: translateZ(0);
  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
