@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #C0DBD9; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.header {
  padding: 30px 0;
  min-height: 100px;
  background-color: #7FAFF6; }
  .header .container {
    display: flex;
    padding-right: 30px;
    padding-left: 30px; }

.footer {
  padding: 40px 0;
  min-height: 100px;
  background-color: #7FAFF6; }

.button {
  display: inline-flex;
  padding: 8px 15px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
  background-color: #A5C7F9;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transform: translateZ(0);
  transition: opacity 0.3s ease; }
  .button:disabled {
    opacity: 0.5;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .button:hover, .button:focus {
      opacity: 0.8; }
    .button:active {
      opacity: 0.5; } }

.pop-up__input-checkbox:checked + .pop-up__block {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
  display: flex;
  pointer-events: auto; }

.pop-up__block {
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  padding: 15px;
  visibility: hidden;
  opacity: 0;
  transform: scale(1.2);
  pointer-events: none;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1); }

.pop-up__overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6); }

.pop-up__content {
  position: relative;
  z-index: 11;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 15px;
  width: 100%;
  max-width: 400px;
  height: 400px;
  background-color: #ffffff; }
  .pop-up__content span {
    font-weight: 400;
    font-size: 53px;
    line-height: 62px;
    color: #000000; }

.pop-up__button-close {
  position: absolute;
  right: 15px;
  top: 15px;
  min-width: 43px;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: #ffffff;
  background-color: #7FAFF6;
  border-radius: 5px; }

.block-green {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  width: calc(100% + 15px);
  margin-bottom: -15px;
  margin-top: 15px;
  list-style: none;
  counter-reset: list-counter; }
  .block-green::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 15px);
    height: calc(100% - 15px);
    background-color: #B3F95A; }

.block-green__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  margin-right: 15px;
  margin-bottom: 15px;
  width: calc(50% - 15px);
  min-width: 160px;
  min-height: 198px;
  background-color: #8AD191;
  counter-increment: list-counter; }
  .block-green__item::before {
    position: absolute;
    content: counter(list-counter);
    font-weight: 400;
    font-size: 53px;
    line-height: 62px;
    color: #ffffff; }
  .block-green__item:first-child {
    width: calc(33.123% - 15px); }
  .block-green__item:not(.block-green__item:first-child):nth-child(2n+1) {
    width: calc(9% - 15px); }
  .block-green__item:nth-child(4n) {
    width: calc(79.5% - 15px); }

.block-blue {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2.704%;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 2.561%;
  background-color: #88B5BF; }

.block-blue__item {
  background-color: #0D89B0;
  width: 100%;
  padding-top: 78.04%;
  position: relative; }

.block-blue__content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

