.block-green {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  padding: 0;

  width: calc(100% + 15px);
  margin-bottom: -15px;

  margin-top: 15px;

  list-style: none;
  counter-reset: list-counter;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 15px);
    height: calc(100% - 15px);

    background-color: #B3F95A;
  }
}

.block-green__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;

  margin-right: 15px;
  margin-bottom: 15px;

  width: calc(50% - 15px);
  min-width: 160px;
  min-height: 198px;

  background-color: #8AD191;

  counter-increment: list-counter;

  &::before {
    position: absolute;
    content: counter(list-counter);
    font-weight: 400;
    font-size: 53px;
    line-height: 62px;
    color: $color-default-white;

  }

  &:first-child {
    width: calc(33.123% - 15px);
  }

  &:not(&:first-child):nth-child(2n+1) {
    width: calc(9% - 15px);
  }

  &:nth-child(4n) {
    width: calc(79.5% - 15px);
  }
}
