.pop-up__input-checkbox {
  &:checked + .pop-up__block {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    display: flex;
    pointer-events: auto;
  }
}

.pop-up__block {
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  display: flex;
  padding: 15px;

  visibility: hidden;
  opacity: 0;
  transform: scale(1.2);
  pointer-events: none;

  transition: opacity $trans-modal, transform $trans-modal, visibility $trans-modal;
}

.pop-up__overlay {
  position: absolute;

  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: transparentize($color-default-black, 0.4);
}

.pop-up__content {
  position: relative;
  z-index: 11;

  display: flex;
  align-items: center;
  justify-content: center;

  margin: auto;
  padding: 15px;
  width: 100%;
  max-width: 400px;
  height: 400px;

  background-color: $color-default-white;

  span {
    font-weight: 400;
    font-size: 53px;
    line-height: 62px;
    color: $color-default-black;
  }
}

.pop-up__button-close {
  position: absolute;
  right: 15px;
  top: 15px;
  min-width: 43px;

  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: $color-default-white;

  background-color: #7FAFF6;
  border-radius: 5px;
}
