.block-blue {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2.704%;
  // можно оставить фиксированный отсутп gap: 30%, это не будет противоречить ТЗ, пропорционально должен меняться только блок. Про отступы ничего не сказано. Но при масштабировании отступы становяться очень большими, поэтому принял решение выставить в %.

  margin-top: 15px;
  margin-bottom: 15px;
  padding: 2.561%;
  // можно оставить фиксированный отсутп padding: 30px;, это не будет противоречить ТЗ, пропорционально должен меняться только блок. Про отступы ничего не сказано. Но при масштабировании отступы становяться очень большими, поэтому принял решение выставить в %.
  background-color: #88B5BF;
}

.block-blue__item {
  background-color: #0D89B0;
  width: 100%;
  padding-top: 78.04%;
  position: relative;
}

.block-blue__content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
